<template>
	<view class="answerView-container" @touchstart="touchStart" @touchend="touchEnd">
		<view class="topic" >
			<view>{{topic[index].title}}</view>
		</view>
	</view>
</template>

<script>
	export default{
		data() {
			return {
				startX: 0, // 滑动开始x轴位置
				moveX: 0, // 滑动的x轴距离
				index: 0, // 当前的第几题
				topic: [ // 题目
					{
						title: '第1题'
					},
					{
						title: '第2题'
					},
					{
						title: '第3题'
					},
					{
						title: '第4题'
					},
					{
						title: '第5题'
					},
					{
						title: '第6题'
					},
					{
						title: '第7题'
					},
					{
						title: '第8题'
					},
					{
						title: '第9题'
					},
					{
						title: '第10题'
					}
				]
			}
		},
		methods: {
			touchStart(e) { // 手指触摸屏幕时触发，有一个手指放在屏幕上也会触发
				// 获取触摸时的原点
				this.startX = e.changedTouches[0].pageX // 触摸目标在页面中的X坐标
				console.log('开始触摸：', this.startX);
			},
			touchEnd(e) { // 手指离开屏幕时触发
				// 获取滑动距离
				console.log(e);
				const moveX = e.changedTouches[0].pageX - this.startX
				console.log(moveX);
				// 判断滑动方向 Math.abs取绝对值
				if (moveX < -100 && this.index<this.topic.length-1) {
					// 左滑（下一题）判断大幅度左滑且不是最后一题
					this.index= this.index+1
					console.log('第'+this.index+'题');
				}
				else if (moveX > 100 && this.index!= 0) {
					// 右滑（上一题）判断大幅度右滑且不是第一题
					this.index=this.index-1
					console.log(this.index);
				}
			}
		}
	}
</script>

<style lang="scss" scoped>
	.answerView-container{
		position: absolute;
		width: 100%;
		height: 100%;
		background-color: #eee;
		display: flex;
		justify-content: center;
		align-items: center;
		.topic{
			width: 80%;
			height: 800rpx;
			background-color: red;
			border: 10rpx solid black;
		}
	}
</style>